<template lang="pug">
    div.full-width.full-height
        loomingCueDefSVG
        SVG2
</template>

<script>
import loomingCueDefSVG from "@/assets/images_src/test.svg";
import SVG2 from "@/assets/images_src/test_2.svg";
import { TweenMax, TimelineMax } from "gsap";
export default {
  name: "LoomingCueDef",
  components: {
    loomingCueDefSVG,
    SVG2
  },
  props: {
    step: {
      type: Number,
      default: 1
    }
  },
  data: function() {
    return {
      timeline: undefined
    };
  },
  watch: {
    step: function(val) {
      if (val <= 2) {
        this.controlTimeline(val);
      } else if (val == 3) {
        TweenMax.killAll();
        const XrelPos = 120; // longitudinal distance travelled
        const duration = 3;
        const tl = new TimelineMax();

        tl.to(
          "#circle2",
          duration,
          {
            x: "+=" + XrelPos,
            ease: Power1.easeIn
          },
          "start"
        );
        tl.to(
          "#circle3",
          duration,
          {
            x: "+=" + XrelPos,
            ease: Sine.easeIn
          },
          "start"
        );
        tl.to(
          "#pathTest3",
          duration,
          {
            attr: {
              d:
                "M" +
                (XrelPos + 22.602) +
                " 50.694l" +
                (160.302 - XrelPos) +
                "-5.665"
            },
            ease: Power1.easeIn
          },
          "start"
        );
        tl.to(
          "#pathTest4",
          duration,
          {
            attr: {
              d:
                "M" +
                (XrelPos + 22.602) +
                " 50.694l" +
                (160.302 - XrelPos) +
                "-5.665"
            },
            ease: Sine.easeIn
          },
          "start"
        );
      }
    }
  },
  beforeDestroy() {
    TweenMax.killAll();
  },
  mounted: function() {
    const XrelPos = 120; // longitudinal distance travelled
    const duration = 3;
    const tl = new TimelineMax();

    tl.to(
      "#circle",
      duration,
      {
        x: "+=" + XrelPos,
        ease: Power1.easeIn
      },
      "start"
    );
    tl.to(
      "#pathTest1",
      duration,
      {
        attr: {
          d:
            "M" +
            (XrelPos + 22.602) +
            " 50.694l" +
            (160.302 - XrelPos) +
            "-5.665"
        },
        ease: Power1.easeIn
      },
      "start"
    );
    tl.to(
      "#pathTest2",
      duration,
      {
        attr: {
          d:
            "M" +
            (XrelPos + 22.602) +
            " 50.694l" +
            (160.302 - XrelPos) +
            "-5.665"
        },
        ease: Power1.easeIn
      },
      "start"
    );
    tl.pause();
    this.timeline = tl;
  },
  methods: {
    getAngleArcD: function(angle, r) {
      //const r = 153.373;
      const cx = r,
        cy = -0.112;
      const endPointX = -29.542 + 153.373 - r,
        endPointY = 37.396;
      var startPointX = -r * Math.cos(angle) + (cx + endPointX);
      var startPointY = r * Math.sin(angle) + (cy + endPointY);
      var d =
        "M" +
        startPointX +
        " " +
        startPointY +
        "a " +
        r +
        " " +
        r +
        " 0 01 " +
        (endPointX - startPointX) +
        " " +
        (endPointY - startPointY) +
        "l" +
        cx +
        " " +
        cy +
        "z";
      return d;
    },
    controlTimeline: function(step) {
      if (step == 2) {
        this.timeline.play("start");
        console.log(TweenMax.getAllTweens());
      } else {
        this.timeline.reverse();
      }
    }
  }
};
</script>

<style scoped>
.red {
  color: rgb(255, 77, 77);
}
</style>
